body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.clock {
  background-color: #fff;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.clock__center {
  z-index: 99;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  bottom: 37.5vmin;
  left: 37.5vmin;
}

.clock__second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-sec;
  position: absolute;
  bottom: 40vmin;
  left: 39.25vmin;
  transform: translateX(-50%)rotate(0);
}

.clock__minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s infinite rotate-minute;
  position: absolute;
  bottom: 40vmin;
  left: 38.5vmin;
  transform: translateX(-50%)rotate(0);
}

@keyframes rotate-sec {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate-minute {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .clock__second-hand {
  animation: 10s linear infinite rotate-sec;
}

.stopwatch--speed-up .clock__minute-hand {
  animation: 600s steps(60, end) infinite rotate-minute;
}
